//MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.cured_children {
		margin-top: 20px;
		height: 2250px;
		position: relative;
		background: inherit;
		width: 100%;
	}

	.cured_children_title {
		@include blockTitleMob2(0px, 35px, 0px, 270px, pre-line);

		position: unset;
		margin-right: 0px;
	}

	.cured_children_title::before {
		content: '';
		position: absolute;
		left: 0px;
		top: 50px;
		height: 3px;
		width: 20px;
		background-color: $arrowcolor;
	}

	.cured_children_amount {
		@include blockTitleMob2(0px, 35px, 0px, 270px, nowrap);

		margin-top: 20px !important;
		color: $arrowcolor;
	}

	.cured_children_carousel {
		margin-left: 15px;
		margin-top: 61px;
		position: relative;
		background: inherit;
		width: 290px;
		height: 2100px;
	}

	.cured_children_carousel_section {
		display: flex !important;
		justify-content: unset;
		flex-wrap: nowrap;
		flex-direction: column;
		width: 100%;
		height: 2000px;
	}

	.cured_children_carousel_section_item {
		width: 290px;
		height: 302px;
		margin-bottom: 20px;
		background-color: #FFFFFF;
		box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
		position: relative;

		a {
			display: inline-block;
			margin-top: 20px;
			margin-left: 20px;
		}

		h3 {
			position: absolute;
			top: 16px;
			left: 130px;
			height: 90px;
			width: 168px;
			color: #333333;
			font-family: $desk-bold-raleway;
			font-size: 18px;
			font-weight: bold;
			line-height: 26px;

			strong {
				font-family: $desk-bold-proxima;
			}
		}

		span {
			display: inline-block;
			margin-top: 20px;
			margin-left: 20px;
			height: 112px;
			width: 80%;
			color: #333333;
			font-family: $desk-regular-raleway;
			font-size: 14px;
			line-height: 24px;

			i {
				font-style: unset;
				font-family: $desk-bold-proxima;
				color: $arrowcolor;
			}
		}
	}

	.cured_children_carousel_section .item5 h3 {
		top: 16px;
	}

	.cured_children_carousel .slick-dots {
		padding-bottom: 10px;
		bottom: 70px;
		left: 1.2%;
		font-family: $desk-regular-proxima;
		font-size: 14px;
		line-height: 17px;
	}

	.cured_children_carousel .slick-dots li {
		opacity: 0.5;
		color: #A5A5A5;
		height: 17px;
		width: 6px;
		font-family: $desk-regular-proxima;
		font-size: 14px;
		line-height: 17px;
		left: -1.5% !important;
		margin-right: 10px;
		margin-bottom: 24px;
	}

	.cured_children_carousel .slick-dots li.slick-active {
		color: #E78B48;
		font-weight: bold;
		opacity: 1;
	}

	.cured_children_carousel .slick-arrow {
		z-index: 9;
		padding: 20px;
	}

	.cured_children_carousel .slick-prev {
		top: 94.4%;
		left: 18.3%;
	}

	.cured_children_carousel .slick-next {
		top: 94.4%;
		left: 65%;
	}

	.cured_children_carousel .slick-prev:before {
		content: "";
		font-size: 10px;
		color: #A5A5A5;
	}

	.cured_children_carousel .slick-next:before {
		content: "";
		color: #A5A5A5;
		font-size: 10px;
	}

	.cured_children_carousel_arrows_far_prev {
		position: absolute;
		color: #A5A5A5;
		font-size: 10px;
		padding: 10px;
		top: 2180.5px;
		left: 21%;
		z-index: 10;
		cursor: pointer;
	}

	.cured_children_carousel_arrows_far_next {
		position: absolute;
		color: #A5A5A5;
		font-size: 10px;
		padding: 10px;
		top: 2180.5px;
		left: 70%;
		z-index: 10;
		cursor: pointer;
	}

	.cured_children_carousel_arrows_far_prev::before {
		content: '|';
		position: absolute;
		top: 11px;
		right: 15px;
	}

	.cured_children_carousel_arrows_far_next::after {
		content: '|';
		position: absolute;
		top: 11px;
		left: 15px;
	}

	.cured_children_carousel_pages {
		display: inline-block;
		position: absolute;
		bottom: 0;
		left: 33%;
		font-style: normal;
		height: 17px;
		width: 108px;
		color: $arrowcolor;
		font-family: $desk-bold-proxima;
		font-size: 14px;
		font-weight: bold;
		line-height: 17px;
		white-space: nowrap;
	}
}

