		top: 320px;
		height: 150px;
		width: 290px;
		background-color: #FFFFFF;
		box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
		cursor: pointer;

		a.download_link {
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 5;
		}
	}

