		display: inline-block;
		position: relative;
		height: 150px;
		width: 290px;
		margin-left: 15px;
		background-color: #FFFFFF;
		box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
		cursor: pointer;

		a.download_link {
			width: 100%;
