@import "partials/reporting_partials/cured_children_mobile";

.home_header_bottom_nav li a sup {
	top: -5.5px !important;
}

.all_reports {
	margin-top: 60px;
	height: 600px;
	min-height: 550px;
	position: relative;
	border-top: 2px solid $arrowcolor;
	background: inherit;
	width: 100%;
}

.all_reports_2017,
.all_reports_2016,
.all_reports_2015,
.all_reports_2014,
.all_reports_2013,
.all_reports_2012,
.all_reports_2011,
.all_reports_2010,
.all_reports_2009,
.all_reports_2008 {
	display: inline-block;
	position: absolute;
	top: 105px;
	white-space: nowrap;
	height: 20px;
	width: 36px;
	padding-bottom: 37px;
	opacity: 0.3;
	color: #333333;
	font-family: $desk-bold-proxima;
	font-size: 16px;
	line-height: 20px;
	border: none;
	cursor: pointer;

	&:hover,
	&:focus {
		color: $arrowcolor;
		border-bottom: 2px solid $arrowcolor;
		opacity: 1;
	}
}

.active-click {
	color: $arrowcolor;
	border-bottom: 2px solid $arrowcolor;
	opacity: 1;
}

.all_reports_2017 {
	left: 113px;
}

.all_reports_2016 {
	left: 234px;
}

.all_reports_2015 {
	left: 356px;
}

.all_reports_2014 {
	left: 478px;
}

.all_reports_2013 {
	left: 600px;
}

.all_reports_2012 {
	left: 722px;
}

.all_reports_2011 {
	left: 844px;
}

.all_reports_2010 {
	left: 963px;
}

.all_reports_2009 {
	left: 1086px;
}

.all_reports_2008 {
	left: 1212px;
}

.all_reports_line {
	top: 180px;
	left: 113px;
	position: absolute;
	background-color: #D8D8D8;
	height: 1px;
	width: 1140px;
}

.all_reports_tax_reports_title {
	@include blockTitle(301px, 115px, 40px, 273px, nowrap);

	margin-right: 313px;
}

.all_reports_annual_reports_title {
	@include blockTitle(301px, 0px, 40px, 273px, nowrap);
}

.all_reports_tax_reports_title::before {
	content: '';
	position: absolute;
	left: -113px;
	top: 27px;
	height: 3px;
	width: 58px;
	background-color: $arrowcolor;
}

.tax_reports_container {
	display: inline-block;
	position: relative;
	margin-left: 113px;
	margin-right: 40px;
	height: 150px;
	width: 550px;
	background-color: #FFFFFF;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
	cursor: pointer;

	a.download_link {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 5;
	}
}

.annual_reports_container {
	display: inline-block;
	position: relative;
	height: 150px;
	width: 550px;
	background-color: #FFFFFF;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
	cursor: pointer;

	a.download_link {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 5;
	}
}

.tax_reports_container .tax_reports_container_title,
.annual_reports_container .annual_reports_container_title {
	display: inline-block;
	margin-top: 44px;
	margin-left: 41px;
	height: 26px;
	width: 266px;
	color: #333333;
	font-family: $desk-bold-raleway;
	font-size: 20px;
	font-weight: bold;
	line-height: 26px;
	white-space: nowrap;

	strong {
		font-family: $desk-bold-proxima;
	}
}

.tax_reports_container .tax_reports_container_download,
.annual_reports_container .annual_reports_container_download {
	position: absolute;
	bottom: 44px;
	left: 41px;
	height: 16px;
	width: 91px;
	color: $arrowcolor;
	font-family: $desk-bold-raleway;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
}

.tax_reports_container .tax_reports_container_download::after,
.annual_reports_container .annual_reports_container_download::after {
	content: '';
	position: absolute;
	top: 2px;
	left: 105px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.tax_reports_container:hover,
.tax_reports_container:focus {
	background: $Bghover-color;

	.tax_reports_container_title {
		color: #fff;
	}

	.tax_reports_container_download {
		color: #fff;
	}

	.tax_reports_container_download::after {
		background: url("../img/svg/arrow_right_white.svg") no-repeat center;
	}
}

.annual_reports_container:hover,
.annual_reports_container:focus {
	background: $Bghover-color;

	.annual_reports_container_title {
		color: #fff;
	}

	.annual_reports_container_download {
		color: #fff;
	}

	.annual_reports_container_download::after {
		background: url("../img/svg/arrow_right_white.svg") no-repeat center;
	}
}

.displayBlockTab {
	display: inline-block !important;
}

