.annual_report_2010,
.tax_report_2010,
.annual_report_2009,
.tax_report_2009,
.annual_report_2008,
.tax_report_2008 {
	display: none;
}

.cured_children {
	margin-top: 70px;
	height: 841px;
	position: relative;
	background: inherit;
	width: 100%;
}

.cured_children_title {
	@include blockTitle(0px, 115px, 0px, 273px, nowrap);

	margin-right: 320px;
}

.cured_children_title::before {
	content: '';
	position: absolute;
	left: -113px;
	top: 27px;
	height: 3px;
	width: 58px;
	background-color: $arrowcolor;
}

.cured_children_amount {
	@include blockTitle(0px, 115px, 0px, 416px, nowrap);

	height: 52px;
	font-size: 42px;
	line-height: 52px;
	font-family: $desk-bold-proxima;
	color: $arrowcolor;
}

.cured_children_carousel {
	margin-left: 115px;
	margin-top: 61px;
	position: relative;
	background: inherit;
	width: 1140px;
	height: 824px;
}

.cured_children_carousel_section {
	display: flex !important;
	justify-content: space-between;
	flex-wrap: wrap;
	width: 100%;
	height: 690px;
}

.cured_children_carousel_section_item {
	width: 353px;
	height: 302px;
	background-color: #FFFFFF;
	box-shadow: 4px 4px 30px 0 rgba(209, 209, 209, 0.2);
	position: relative;

	a {
		display: inline-block;
		margin-top: 38px;
		margin-left: 40px;
	}

	h3 {
		position: absolute;
		top: 34px;
		left: 150px;
		height: 90px;
		width: 168px;
		color: #333333;
		font-family: $desk-bold-raleway;
		font-size: 20px;
		font-weight: bold;
		line-height: 30px;

		strong {
			font-family: $desk-bold-proxima;
		}
	}

	span {
		display: inline-block;
		margin-top: 20px;
		margin-left: 40px;
		height: 112px;
		width: 87%;
		color: #333333;
		font-family: $desk-regular-raleway;
		font-size: 16px;
		line-height: 28px;

		i {
			font-style: normal !important;
			font-family: $desk-bold-proxima;
			color: $arrowcolor;
		}
	}
}

.cured_children_carousel_section .item5 h3 {
	top: 16px;
}

.cured_children_carousel .slick-dots {
	padding-bottom: 10px;
	bottom: 70px;
	left: 1.2%;
	font-family: $desk-regular-proxima;
	font-size: 14px;
	line-height: 17px;
}

.cured_children_carousel .slick-dots li {
	opacity: 0.5;
	color: #A5A5A5;
	height: 17px;
	width: 6px;
	font-family: $desk-regular-proxima;
	font-size: 14px;
	line-height: 17px;
	left: -1.5% !important;
	margin-right: 10px;
	margin-bottom: 24px;
}

.cured_children_carousel .slick-dots li.slick-active {
	color: #E78B48;
	font-weight: bold;
	opacity: 1;
}

.cured_children_carousel .slick-dots li {
	display: none;
}

.cured_children_carousel .slick-dots li.slick-active,
.cured_children_carousel .slick-dots li.slick-active + li,
.cured_children_carousel .slick-dots li.slick-active + li + li,
.cured_children_carousel .slick-dots li.slick-active + li + li + li,
.cured_children_carousel .slick-dots li.slick-active + li + li + li + li,
.cured_children_carousel .slick-dots li.slick-active + li + li + li + li + li {
	display: inline-block !important;
}

.cured_children_carousel .slick-dots li:nth-last-child(1),
.cured_children_carousel .slick-dots li:nth-last-child(2),
.cured_children_carousel .slick-dots li:nth-last-child(3),
.cured_children_carousel .slick-dots li:nth-last-child(4),
.cured_children_carousel .slick-dots li:nth-last-child(5),
.cured_children_carousel .slick-dots li:nth-last-child(6) {
	display: inline-block;
}

.cured_children_carousel .slick-dots li.slick-active ~ li:nth-last-child(1),
.cured_children_carousel .slick-dots li.slick-active ~ li:nth-last-child(2),
.cured_children_carousel .slick-dots li.slick-active ~ li:nth-last-child(3),
.cured_children_carousel .slick-dots li.slick-active ~ li:nth-last-child(4),
.cured_children_carousel .slick-dots li.slick-active ~ li:nth-last-child(5),
.cured_children_carousel .slick-dots li.slick-active ~ li:nth-last-child(6) {
	display: none;
}

.cured_children_carousel .slick-arrow {
	z-index: 9;
	padding: 20px;
}

.cured_children_carousel .slick-prev {
	top: 85.8%;
	left: 39.3%;
}

.cured_children_carousel .slick-next {
	top: 85.8%;
	left: 56.5%;
}

.cured_children_carousel .slick-prev:before {
	content: "";
	font-size: 10px;
	color: #A5A5A5;
}

.cured_children_carousel .slick-next:before {
	content: "";
	color: #A5A5A5;
	font-size: 10px;
}

.cured_children_carousel_arrows_far_prev {
	position: absolute;
	color: #A5A5A5;
	font-size: 10px;
	padding: 10px;
	top: 814.5px;
	left: 552px;
	z-index: 10;
	cursor: pointer;
}

.cured_children_carousel_arrows_far_next {
	position: absolute;
	color: #A5A5A5;
	font-size: 10px;
	padding: 10px;
	top: 814.5px;
	left: 790px;
	z-index: 10;
	cursor: pointer;
}

.cured_children_carousel_arrows_far_prev::before {
	content: '|';
	position: absolute;
	top: 12px;
	right: 15px;
}

.cured_children_carousel_arrows_far_next::after {
	content: '|';
	position: absolute;
	top: 12px;
	left: 15px;
}

.cured_children_carousel_pages {
	display: inline-block;
	position: absolute;
	bottom: 0;
	left: 1142px;
	font-style: normal;
	height: 17px;
	width: 108px;
	color: $arrowcolor;
	font-family: $desk-bold-proxima;
	font-size: 14px;
	font-weight: bold;
	line-height: 17px;
	white-space: nowrap;
}

//FOOTER EXCEPTIONS

.page_footer {
	margin-top: 101px !important;
}

// Footer MEDIA QUERY 320-1024

@media screen and (min-width: 320px) and (max-width: 1024px) {
	.page_footer {
		margin-top: 60px !important;
	}
}
